<template>
    <div class="list" v-if="isReady">
                <div class="item" v-for="(item,index) in list" :key="index">
                <iframe id="player" class="video" :src="embedUrl +item.id.videoId+`?enablejsapi=1&allowsInlineMediaPlayback=true&playsinline=1`" width="300" height="300"></iframe>
                    <div class="detail">
                        <img :src="icon" class="icon">
                        <div class=right>
                            <div class="title">{{item.snippet.title}}</div> 
                            <div class="owner">{{item.snippet.channelTitle}}</div>
                            <p> {{item.snippet.description}}</p>
                            <button @click="addFavorite(item.id.videoId)">add</button>
                        </div>
                    </div>
                </div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name:'Index',
    data(){
        return{
            isReady:false,
            getUserUrl:'https://www.googleapis.com/youtube/v3/channels?part=contentDetails&mine=true&key=AIzaSyAWVhkyOnTdrJOJZizvsUuB8UvIgUH77Ew',
            url:'https://www.googleapis.com/youtube/v3/search?&part=snippet&type=video&videoCaption=closedCaption',
            iconUrl:'https://www.googleapis.com/youtube/v3/channels?part=snippet&id=UCLW_SzI9txZvtOFTPDswxqg&fields=items(id%2Csnippet%2Fthumbnails)&key=AIzaSyAWVhkyOnTdrJOJZizvsUuB8UvIgUH77Ew',
            allListsUrl:'https://www.googleapis.com/youtube/v3/channels?part=contentDetails&key=AIzaSyAWVhkyOnTdrJOJZizvsUuB8UvIgUH77Ew',
            embedUrl:'https://www.youtube.com/embed/',
            addUrl:'https://www.googleapis.com/youtube/v3/playlistItems?part=snippet&key=AIzaSyAWVhkyOnTdrJOJZizvsUuB8UvIgUH77Ew',
            maxResults:'50',
            key:'AIzaSyAWVhkyOnTdrJOJZizvsUuB8UvIgUH77Ew',
            list:[],
            icon:'',
            userId:'',
            favoriteId:'',
            videoId:'',
            accessToken:localStorage.getItem('token') || '',
        }
    },
    methods:{
            getUserInfo(){
                const {getUserUrl} = this
                axios.get(getUserUrl).then(({data})=>{
                    this.userId = data.items[0].id
                    this.getAllList()
                    })
            },
            getIcon(){
                const { iconUrl } = this
                axios.get(iconUrl).then(({data})=>{  
                    this.icon = data.items[0].snippet.thumbnails.default.url
                    this.isReady = true
                })
            },
            getList(){
                console.log(this.accessToken)
                  const { url, maxResults, key } = this
                const combinedUrl = url + `&q=${encodeURIComponent('木曜4超玩')}/videos&maxResults=${maxResults}&key=${key}`
                axios.get(combinedUrl).then(({data})=>{
                    this.list = data.items
                    this.getIcon()
                })
            },
            getAllList(){
                const {allListsUrl, userId} = this
                axios.get(allListsUrl +`&id=${userId}`).then(({data})=>{
                     this.favoriteId = data.items[0].contentDetails.relatedPlaylists.favorites
                })
            },
            addFavorite(videoId){
                const {favoriteId}= this
                this.videoId = videoId
                const data ={
                     snippet: {
                        playlistId: favoriteId,
                        position: 0,
                        resourceId: {
                        kind: "youtube#video",
                        videoId
                        }
                    }
                }
                const {addUrl,authenticate,accessToken} = this
                if(accessToken){
                    axios.post(addUrl,data).then(()=>{
                                console.log('success')
                                alert('增加成功')
                        }).catch(()=>{
                            authenticate()
                        })
                }else{
                     authenticate()
                }
            },
             authenticate() {
                return gapi.auth2.getAuthInstance()
                    .signIn({scope: "https://www.googleapis.com/auth/youtube.force-ssl https://www.googleapis.com/auth/youtube.readonly"})
                    .then((response)=> {
                        this.accessToken = response.wc.access_token
                        localStorage.token = response.wc.access_token
                        this.setting()        
                    }).catch((err)=>{
                            console.error("Error signing in", err);
                    })
            },
            setting(){
                    axios.interceptors.request.use(
                    (config) => {
                      config.headers.Authorization  = `Bearer ${localStorage.getItem('token')}` || this.accessToken
                      this.loadClient()
                        return config
                    },
                        err => Promise.reject(err),
                    )
            },
            loadClient() {
                gapi.client.setApiKey("AIzaSyAWVhkyOnTdrJOJZizvsUuB8UvIgUH77Ew");
                return gapi.client.load("https://www.googleapis.com/discovery/v1/apis/youtube/v3/rest")
                    .then(()=> { 
                    this.execute()
                    }).catch(()=>{
                        console.error("Error loading GAPI client for API")
                    })
            },
            execute() {
                     const {videoId, favoriteId} = this
                    return gapi.client.youtube.playlistItems.insert({
                    "part": [
                        "snippet"
                    ],
                    "resource": {
                        "snippet": {
                        "playlistId": favoriteId,
                        "position": 0,
                        "resourceId": {
                            "kind": "youtube#video",
                            "videoId": videoId
                                }
                            }
                        }
                    })
                    .then((response) =>{
                        console.log("Response", response);
                        alert('增加成功')
                    }).catch((err)=>{
                        console.error("Execute error", err);
                    })
            }
    },
    created(){
        axios.defaults.withCredentials = false
          gapi.load("client:auth2", function() {
            gapi.auth2.init({client_id: '878648823639-smhdtm84lnasgdg2vcf882tacri1fn5i.apps.googleusercontent.com',scope: 'https://www.googleapis.com/auth/youtube',});
        });
        this.getUserInfo()
        this.getList()
    },
}
</script>
